import * as React from 'react'
import { render } from 'react-dom'
import { Router, Route, hashHistory } from 'react-router'
import * as $ from 'jquery'

import { Loading } from './Loading';


export class App extends React.Component<any, any>
{
    constructor(props: any) {
        super(props);
        this.state = (this.state = this.props);
        // this.state = {
        //     data: JSON.parse(localStorage.getItem('Headline'))
        // };
        this.fillData();
    }



    private fetch(key?: string, callback?: any) {
        var result = localStorage.getItem(key);

        result ? callback(JSON.parse(result)) :
            $.getJSON("/list",
                cs => {
                    //this.state = cs;
                    let aa = [];
                    aa = cs;
                    result = cs;
                    result && localStorage.setItem(key, JSON.stringify(result));
                    this.setState({ data: result });
                    //return aa;
                });


        callback(result);
    }



    private fillData() {
        this.fetch('Headline', (result:any) => {
            this.setState({ data: result });
        });
    }


    public render() {
        if (this.state.data) {
            return <div>
                <button className="btn btn-xs primary" onClick={(e) => this.fillData()}>Get</button>

                <table>
                    <thead>
                        <th>
                            url
                    </th>
                        <th>
                            gsm
                    </th>
                        <th>
                            l.c.
                    </th>
                        <th>
                            c.c.
                    </th>
                        <th>
                            bck
                    </th>
                        <th>
                            tr.
                    </th>
                        <th>
                            pri.
                    </th>
                        <th>
                            fav.
                    </th>
                        <th>

                        </th>
                    </thead>
                    <tbody>
                        {
                            this.state.data.Headline.map(function (item: any, index: number) {
                                return <tr key={item.ARTICLEID}>
                                    <td>{item.ARTICLEID}</td>
                                    <td>{item.TITLE}</td>
                                    <td>{item.SPOT}</td>
                                    <td>{item.CategoryName}</td>
                                    <td>{item.TITLE2.toString()}</td>
                                    <td></td>
                                    <td>{item.RowNumber}</td>
                                    <td></td>
                                    <td>
                                        <button className="btn btn-xs warning"><i className="fa fa-arrow-circle-up"></i></button>

                                        <a className="btn btn-xs btn-danger"><i className="fa fa-remove"></i></a>
                                        <a className="btn btn-xs btn-success"><i className="fa fa-check"></i></a>
                                    </td>
                                </tr>;
                            })}
                    </tbody>

                </table>
            </div>
        }
        return <Loading />;;
    }
}


